it('close')\" title=\"\">\n        <i class=\"fa-solid fa-times\"></i>\n      </button>\n    </div>\n    \n    <div class=\"gc-result-body\">\n      <div class=\"gc-level-desc\">\n        <i class=\"fa-solid fa-quote-left\"></i>\n        {{ result. }}\n      </div>\n      <div class=\"gc-result-desc\">{{ result. }}</div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport type { GiantessData, TinyData } from '../core';\n\ndefineProps<{\n  result: GiantessData | TinyData;\n  isGiant: boolean;\n}>();\n\ndefineEmits<{\n  (e: 'close'): void;\n}>();\n</script>\n\n<style scoped>\n.gc-result {\n  --gc-giant-color: #fb923c;\n  --gc-tiny-color: #60a5fa;\n  --gc-bg: rgba(30, 41, 59, 0.7);\n  --gc-border: rgba(255, 255, 255, 0.1);\n  \n  background: var(--gc-bg);\n  border: 1px solid var(--gc-border);\n  border-radius: 12px;\n  overflow: hidden;\n  margin-bottom: 16px;\n  transition: transform 0.2s, box-shadow 0.2s;\n  backdrop-filter: blur(8px);\n}\n\n.gc-result:hover {\n  transform: translateY(-2px);\n  box-shadow: 0 8px 16px -4px rgba(0, 0, 0, 0.2);\n}\n\n.gc-result.giant {\n  border-left: 4px solid var(--gc-giant-color);\n}\n\n.gc-result.tiny {\n  border-left: 4px solid var(--gc-tiny-color);\n}\n\n.gc-result-header {\n  padding: 16px;\n  display: flex;\n  align-items: center;\n  gap: 16px;\n  border-bottom: 1px solid var(--gc-border);\n  background: rgba(255, 255, 255, 0.03);\n  position: relative;\n}\n\n.gc-result-close {\n  position: absolute;\n  top: 8px;\n  right: 8px;\n  width: 28px;\n  height: 28px;\n  border: none;\n  border-radius: 6px;\n  background: rgba(255, 255, 255, 0.05);\n  color: rgba(255, 255, 255, 0.5);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s;\n}\n\n.gc-result-close:hover {\n  background: rgba(239, 68, 68, 0.2);\n  color: #ef4444;\n}\n\n.gc-result-icon-wrapper {\n  width: 48px;\n  height: 48px;\n  border-radius: 12px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 1.5em;\n  flex-shrink: 0;\n  background: rgba(255, 255, 255, 0.05);\n}\n\n.gc-result.giant .gc-result-icon-wrapper {\n  color: var(--gc-giant-color);\n  background: rgba(251, 146, 60, 0.1);\n}\n\n.gc-result.tiny .gc-result-icon-wrapper {\n  color: var(--gc-tiny-color);\n  background: rgba(96, 165, 250, 0.1);\n}\n\n.gc-result-title {\n  flex: 1;\n}\n\n.gc-result-height {\n  font-size: 1.5em;\n  font-weight: 700;\n  line-height: 1.2;\n  color: var(--smart-text-color, #e2e8f0);\n  letter-spacing: -0.02em;\n}\n\n.gc-result-meta {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  margin-top: 4px;\n  font-size: 0.85em;\n}\n\n.gc-badge {\n  padding: 2px 8px;\n  border-radius: 6px;\n  font-weight: 600;\n  text-transform: uppercase;\n  font-size: 0.85em;\n}\n\n.gc-badge.giant {\n  background: rgba(251, 146, 60, 0.2);\n  color: var(--gc-giant-color);\n}\n\n.gc-badge.tiny {\n  background: rgba(96, 165, 250, 0.2);\n  color: var(--gc-tiny-color);\n}\n\n.gc-multiplier {\n  color: var(--smart-text-muted-color, #94a3b8);\n  font-family: 'JetBrains Mono', monospace;\n}\n\n.gc-result-body {\n  padding: 16px;\n}\n\n.gc-level-desc {\n  font-size: 0.9em;\n  color: var(--smart-text-muted-color, #94a3b8);\n  margin-bottom: 12px;\n  font-style: italic;\n  display: flex;\n  gap: 8px;\n}\n\n.gc-level-desc i {\n  opacity: 0.5;\n  margin-top: 2px;\n}\n\n.gc-result-desc {\n  font-size: 0.95em;\n  line-height: 1.6;\n  color: var(--smart-text-color, #e2e8f0);\n  white-space: pre-wrap;\n}\n</style>"],"sourceRoot":""}]);
