`, "",{"version":3,"sources":["webpack://./src/ui/panels/SettingsPanel.vue"],"names":[],"mappings":"AAsHA,mCACE,YAAa,CACb,qBAAsB,CACtB,QACF,CAEA,kCACE,YAAa,CACb,qBAAsB,CACtB,OACF,CAEA,wCACE,gBAAiB,CACjB,kCAAoC,CACpC,eACF,CAEA,2BACE,gBAAiB,CACjB,0BAA8B,CAC9B,uDAA4D,CAC5D,iBAAkB,CAClB,4BAA8B,CAC9B,eACF,CAEA,iCACE,YAAa,CACb,sCACF,CAEA,8BACE,eAAgB,CAChB,gBACF,CAEA,mCACE,YAAa,CACb,kBAAmB,CACnB,OACF,CAEA,0CACE,UAAW,CACX,WAAY,CACZ,iCAAqC,CACrC,uDAA4D,CAC5D,iBAAkB,CAClB,4BAA8B,CAC9B,cACF,CAEA,gDACE,gCACF,CAEA,yCACE,UAAW,CACX,iBAAkB,CAClB,0BAA8B,CAC9B,uDAA4D,CAC5D,iBAAkB,CAClB,4BAA8B,CAC9B,eAAgB,CAChB,WACF,CAEA,oCACE,YAAa,CACb,6BAA8B,CAC9B,sBAAuB,CACvB,aAAc,CACd,8DACF,CAEA,0CACE,YAAa,CACb,qBAAsB,CACtB,OACF,CAEA,+CACE,eAAgB,CAChB,eACF,CAEA,gDACE,gBAAiB,CACjB,kCACF","sourcesContent":["<template>\n  <GcOverlay\n    :visible=\"visible\"\n    title=\"\"\n    icon=\"fa-solid fa-gear\"\n    @close=\"$emit('close')\"\n  >\n    <div class=\"gc-settings-list\">\n      <div class=\"gc-setting-item\">\n        <label></label>\n        <input\n          :value=\"settings.variablePrefix\"\n          @input=\"updateSetting('variablePrefix', ($event.target as HTMLInputElement).value)\"\n          type=\"text\"\n          class=\"gc-input sm\"\n          placeholder=\"\"\n        />\n      </div>\n      \n      <div class=\"gc-setting-item\">\n        <label></label>\n        <div class=\"gc-input-spinner\">\n          <button @click=\"decrementDepth\">-</button>\n          <input :value=\"settings.injectDepth\" type=\"number\" readonly />\n          <button @click=\"incrementDepth\">+</button>\n        </div>\n      </div>\n      \n      <div class=\"gc-setting-toggle\">\n        <label>\n          <span></span>\n          <small> Prompt</small>\n        </label>\n        <GcSwitch\n          :model-value=\"settings.autoInject\"\n          @update:model-value=\"updateSetting('autoInject', $event)\"\n        />\n      </div>\n      \n      <div class=\"gc-setting-toggle\">\n        <label>\n          <span></span>\n          <small></small>\n        </label>\n        <GcSwitch\n          :model-value=\"settings.injectInteractionLimits\"\n          @update:model-value=\"updateSetting('injectInteractionLimits', $event)\"\n        />\n      </div>\n      \n      <div class=\"gc-setting-toggle\">\n        <label>\n          <span></span>\n          <small></small>\n        </label>\n        <GcSwitch\n          :model-value=\"settings.debug\"\n          @update:model-value=\"updateSetting('debug', $event)\"\n        />\n      </div>\n      \n      <div class=\"gc-setting-toggle\">\n        <label>\n          <span></span>\n          <small> AI </small>\n        </label>\n        <GcSwitch\n          :model-value=\"settings.showVariableUpdateRules\"\n          @update:model-value=\"updateSetting('showVariableUpdateRules', $event)\"\n        />\n      </div>\n      \n      <div class=\"gc-setting-toggle\">\n        <label>\n          <span></span>\n          <small></small>\n        </label>\n        <GcSwitch\n          :model-value=\"settings.compactPromptFormat\"\n          @update:model-value=\"updateSetting('compactPromptFormat', $event)\"\n        />\n      </div>\n    </div>\n  </GcOverlay>\n</template>\n\n<script setup lang=\"ts\">\nimport { GcOverlay, GcSwitch } from '../components';\nimport type { Settings } from '../../types';\n\nconst props = defineProps<{\n  visible: boolean;\n  settings: Settings;\n}>();\n\nconst emit = defineEmits<{\n  (e: 'close'): void;\n  (e: 'update:setting', key: keyof Settings, value: unknown): void;\n}>();\n\nconst updateSetting = (key: keyof Settings, value: unknown) => {\n  emit('update:setting', key, value);\n};\n\nconst decrementDepth = () => {\n  if (props.settings.injectDepth > 0) {\n    updateSetting('injectDepth', props.settings.injectDepth - 1);\n  }\n};\n\nconst incrementDepth = () => {\n  if (props.settings.injectDepth < 10) {\n    updateSetting('injectDepth', props.settings.injectDepth + 1);\n  }\n};\n</script>\n\n<style scoped>\n.gc-settings-list {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.gc-setting-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.gc-setting-item label {\n  font-size: 0.85em;\n  color: var(--gc-text-muted, #94a3b8);\n  font-weight: 500;\n}\n\n.gc-input {\n  padding: 6px 10px;\n  background: rgba(0, 0, 0, 0.2);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 6px;\n  color: var(--gc-text, #f1f5f9);\n  font-size: 0.9em;\n}\n\n.gc-input:focus {\n  outline: none;\n  border-color: var(--gc-primary, #f472b6);\n}\n\n.gc-input.sm {\n  padding: 4px 8px;\n  font-size: 0.85em;\n}\n\n.gc-input-spinner {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.gc-input-spinner button {\n  width: 28px;\n  height: 28px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 4px;\n  color: var(--gc-text, #f1f5f9);\n  cursor: pointer;\n}\n\n.gc-input-spinner button:hover {\n  background: rgba(255, 255, 255, 0.1);\n}\n\n.gc-input-spinner input {\n  width: 50px;\n  text-align: center;\n  background: rgba(0, 0, 0, 0.2);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 4px;\n  color: var(--gc-text, #f1f5f9);\n  font-size: 0.9em;\n  padding: 4px;\n}\n\n.gc-setting-toggle {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-start;\n  padding: 8px 0;\n  border-bottom: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n}\n\n.gc-setting-toggle label {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n}\n\n.gc-setting-toggle label span {\n  font-size: 0.9em;\n  font-weight: 500;\n}\n\n.gc-setting-toggle label small {\n  font-size: 0.75em;\n  color: var(--gc-text-muted, #94a3b8);\n}\n</style>\n"],"sourceRoot":""}]);
