low:hidden}.gc-control-bar[data-v-2fd76fdc]{margin-bottom:16px}.gc-master-btn[data-v-2fd76fdc]{width:100%;padding:12px 16px;background:rgba(0,0,0,0.3);border:1px solid var(--gc-border);border-radius:12px;color:var(--gc-text-muted);cursor:pointer;display:flex;align-items:center;justify-content:space-between;gap:12px;font-size:1em;transition:all 0.3s cubic-bezier(0.4,0,0.2,1);position:relative;overflow:hidden}.gc-master-btn[data-v-2fd76fdc]:hover{background:rgba(255,255,255,0.05);border-color:rgba(255,255,255,0.15)}.gc-master-btn.active[data-v-2fd76fdc]{background:linear-gradient(135deg,rgba(236,72,153,0.2) 0%,rgba(168,85,247,0.2) 100%);border-color:rgba(236,72,153,0.4);color:white;box-shadow:0 0 20px rgba(236,72,153,0.1)}.gc-master-btn.active[data-v-2fd76fdc]:hover{background:linear-gradient(135deg,rgba(236,72,153,0.25) 0%,rgba(168,85,247,0.25) 100%);border-color:rgba(236,72,153,0.5)}.gc-master-icon[data-v-2fd76fdc]{width:32px;height:32px;display:flex;align-items:center;justify-content:center;border-radius:8px;background:rgba(0,0,0,0.2);transition:all 0.3s}.gc-master-btn.active .gc-master-icon[data-v-2fd76fdc]{background:rgba(236,72,153,0.8);color:white;box-shadow:0 0 10px rgba(236,72,153,0.4)}.gc-master-status-dot[data-v-2fd76fdc]{width:8px;height:8px;border-radius:50%;background:#64748b;box-shadow:0 0 0 2px rgba(100,116,139,0.2);transition:all 0.3s}.gc-master-btn.active .gc-master-status-dot[data-v-2fd76fdc]{background:#10b981;box-shadow:0 0 0 2px rgba(16,185,129,0.3),0 0 10px rgba(16,185,129,0.5)}.gc-action-bar[data-v-2fd76fdc]{display:flex;gap:8px;margin-bottom:24px;justify-content:flex-end;align-items:center;padding:6px;background:rgba(0,0,0,0.2);border-radius:14px;border:1px solid rgba(255,255,255,0.05)}.gc-divider[data-v-2fd76fdc]{width:1px;height:20px;background:rgba(255,255,255,0.1);margin:0 4px}.gc-icon-btn[data-v-2fd76fdc]{width:36px;height:36px;display:flex;align-items:center;justify-content:center;border-radius:10px;border:none;background:transparent;color:var(--gc-text-muted,#94a3b8);cursor:pointer;transition:all 0.2s ease;padding:0;margin:0;box-shadow:none}.gc-icon-btn[data-v-2fd76fdc]:hover{background:rgba(255,255,255,0.08);color:var(--gc-text,#f8fafc)}.gc-icon-btn.active[data-v-2fd76fdc]{background:rgba(236,72,153,0.15);color:var(--gc-primary,#ec4899)}.gc-icon-btn.debug[data-v-2fd76fdc]{color:#fbbf24}.gc-icon-btn.debug[data-v-2fd76fdc]:hover{background:rgba(251,191,36,0.15)}.gc-icon-btn.debug.active[data-v-2fd76fdc]{background:rgba(251,191,36,0.2)}.gc-icon-btn.prompt[data-v-2fd76fdc]{color:#a78bfa}.gc-icon-btn.prompt[data-v-2fd76fdc]:hover{background:rgba(167,139,250,0.15)}.gc-icon-btn.prompt.active[data-v-2fd76fdc]{background:rgba(167,139,250,0.2)}.gc-icon-btn.worldview[data-v-2fd76fdc]{color:#c084fc}.gc-icon-btn.worldview[data-v-2fd76fdc]:hover{background:rgba(192,132,252,0.15)}.gc-icon-btn.worldview.active[data-v-2fd76fdc]{background:rgba(192,132,252,0.2)}.gc-main-area[data-v-2fd76fdc]{transition:opacity 0.2s}.gc-main-area.disabled[data-v-2fd76fdc]{opacity:0.5;pointer-events:none;filter:grayscale(1)}.gc-section[data-v-2fd76fdc]{margin-bottom:24px}.gc-section-header[data-v-2fd76fdc]{display:flex;align-items:center;justify-content:space-between;margin-bottom:12px;font-size:0.9em;font-weight:600;color:var(--gc-text-muted);text-transform:uppercase;letter-spacing:0.05em}.gc-section-header i[data-v-2fd76fdc]{color:var(--gc-primary);margin-right:8px}.gc-badge-count[data-v-2fd76fdc]{padding:2px 10px;background:rgba(255,255,255,0.08);border-radius:12px;font-size:0.85em;color:var(--gc-text);font-weight:500}
