 Result from '../Result.vue';\n\nconst {\n  calcHeight,\n  calcOriginal,\n  calcResult,\n  isGiant,\n  doCalculate,\n  clearCalcResult,\n} = useCalculator();\n</script>\n\n<style scoped>\n.gc-calc-section {\n  margin-bottom: 20px;\n}\n\n.gc-calc-box {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  padding: 6px;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 14px;\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.08));\n}\n\n.gc-input-wrapper {\n  flex: 1;\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n.gc-input {\n  width: 100%;\n  padding: 10px 32px 10px 14px;\n  background: rgba(255, 255, 255, 0.03);\n  border: 1px solid transparent;\n  border-radius: 10px;\n  color: var(--gc-text, #f8fafc);\n  font-size: 0.95em;\n  transition: all 0.2s;\n  font-family: var(--gc-font-mono);\n}\n\n.gc-input:hover {\n  background: rgba(255, 255, 255, 0.06);\n}\n\n.gc-input:focus {\n  outline: none;\n  background: rgba(0, 0, 0, 0.3);\n  border-color: var(--gc-primary, #ec4899);\n  box-shadow: 0 0 0 3px var(--gc-primary-light, rgba(236, 72, 153, 0.15));\n}\n\n.gc-input::placeholder {\n  color: var(--gc-text-dim, #64748b);\n  font-family: var(--gc-font-family);\n}\n\n.gc-unit {\n  position: absolute;\n  right: 12px;\n  color: var(--gc-text-dim, #64748b);\n  font-size: 0.85em;\n  pointer-events: none;\n  font-weight: 500;\n}\n\n.gc-operator {\n  color: var(--gc-text-muted, #94a3b8);\n  font-weight: bold;\n  font-size: 1.1em;\n  opacity: 0.5;\n}\n\n.gc-btn-icon {\n  width: 42px;\n  height: 42px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 10px;\n  border: none;\n  cursor: pointer;\n  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);\n  flex-shrink: 0;\n}\n\n.gc-btn-icon.primary {\n  background: var(--gc-primary, #ec4899);\n  color: white;\n  box-shadow: 0 4px 12px rgba(236, 72, 153, 0.3);\n}\n\n.gc-btn-icon.primary:hover {\n  background: var(--gc-primary-hover, #db2777);\n  transform: translateY(-2px);\n  box-shadow: 0 6px 16px rgba(236, 72, 153, 0.4);\n}\n\n.gc-btn-icon.primary:active {\n  transform: translateY(0);\n}\n\n/*  */\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.fade-enter-from,\n.fade-leave-to {\n  opacity: 0;\n}\n</style>\n"],"sourceRoot":""}]);
