nB,OACF,CAEA,oCACE,4BACF","sourcesContent":["<template>\n  <GcOverlay\n    :visible=\"visible\"\n    title=\"\"\n    icon=\"fa-solid fa-file-lines\"\n    panel-class=\"gc-prompts-panel\"\n    @close=\"handleClose\"\n  >\n    <template #header-actions>\n      <button class=\"gc-btn-link\" @click=\"$emit('reset')\" title=\"\">\n        <i class=\"fa-solid fa-rotate-left\"></i>\n      </button>\n    </template>\n\n    <div class=\"gc-prompts-content\">\n      <!--  -->\n      <div v-if=\"!editingTemplate && !isCreating\" class=\"gc-prompts-list\">\n        <div class=\"gc-prompts-hint\">\n          <i class=\"fa-solid fa-info-circle\"></i>\n          <span>/</span>\n        </div>\n        \n        <div \n          v-for=\"template in templates\" \n          :key=\"template.id\" \n          class=\"gc-prompt-item\"\n          :class=\"{ disabled: !template.enabled, builtin: template.builtin }\"\n        >\n          <div class=\"gc-prompt-toggle\">\n            <GcSwitch\n              :model-value=\"template.enabled\"\n              size=\"sm\"\n              @update:model-value=\"$emit('toggle', template.id)\"\n            />\n          </div>\n          \n          <div class=\"gc-prompt-info\" @click=\"startEdit(template)\">\n            <div class=\"gc-prompt-name\">\n              <span>{{ template.name }}</span>\n              <GcBadge v-if=\"template.builtin\" size=\"sm\"></GcBadge>\n              <GcBadge v-else variant=\"custom\" size=\"sm\"></GcBadge>\n              <span class=\"gc-prompt-type\">{{ template.type }}</span>\n            </div>\n            <div class=\"gc-prompt-desc\">{{ template.description }}</div>\n          </div>\n          \n          <div class=\"gc-prompt-actions\">\n            <div class=\"gc-prompt-order-btns\">\n              <button class=\"gc-btn-icon-xs\" @click=\"$emit('move', template.id, 'up')\" title=\"\">\n                <i class=\"fa-solid fa-chevron-up\"></i>\n              </button>\n              <button class=\"gc-btn-icon-xs\" @click=\"$emit('move', template.id, 'down')\" title=\"\">\n                <i class=\"fa-solid fa-chevron-down\"></i>\n              </button>\n            </div>\n            <button class=\"gc-btn-icon-sm\" @click=\"startEdit(template)\" title=\"\">\n              <i class=\"fa-solid fa-pen\"></i>\n            </button>\n            <button \n              v-if=\"!template.builtin\" \n              class=\"gc-btn-icon-sm danger\" \n              @click=\"$emit('delete', template.id, template.name)\" \n              title=\"\"\n            >\n              <i class=\"fa-solid fa-trash\"></i>\n            </button>\n          </div>\n        </div>\n        \n        <GcButton class=\"gc-add-template-btn\" @click=\"startCreate\">\n          <i class=\"fa-solid fa-plus\"></i> \n        </GcButton>\n      </div>\n\n      <!--  -->\n      <div v-else-if=\"isCreating\" class=\"gc-prompt-editor\">\n        <div class=\"gc-editor-header\">\n          <button class=\"gc-btn-link\" @click=\"cancelCreate\">\n            <i class=\"fa-solid fa-arrow-left\"></i> \n          </button>\n          <span class=\"gc-editor-title\"></span>\n        </div>\n        <div class=\"gc-editor-form\">\n          <div class=\"gc-setting-item\">\n            <label> *</label>\n            <input v-model=\"newTemplate.name\" type=\"text\" class=\"gc-input\" placeholder=\"\" />\n          </div>\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <input v-model=\"newTemplate.description\" type=\"text\" class=\"gc-input\" placeholder=\"\" />\n          </div>\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <select v-model=\"newTemplate.type\" class=\"gc-input\">\n              <option value=\"header\">header - </option>\n              <option value=\"character\">character - </option>\n              <option value=\"interaction\">interaction - </option>\n              <option value=\"rules\">rules - </option>\n              <option value=\"footer\">footer - </option>\n            </select>\n          </div>\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <div class=\"gc-editor-hint\" v-pre>\n              : <code>{{}}</code> <code>{{_}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code>\n            </div>\n            <textarea \n              v-model=\"newTemplate.content\" \n              class=\"gc-textarea gc-editor-textarea\"\n              rows=\"10\"\n              placeholder=\"...\"\n            ></textarea>\n          </div>\n          <div class=\"gc-setting-toggle\">\n            <label><span></span></label>\n            <GcSwitch v-model=\"newTemplate.enabled\" />\n          </div>\n          <div class=\"gc-editor-actions\">\n            <GcButton @click=\"cancelCreate\"></GcButton>\n            <GcButton variant=\"primary\" @click=\"createTemplate\">\n              <i class=\"fa-solid fa-plus\"></i> \n            </GcButton>\n          </div>\n        </div>\n      </div>\n\n      <!--  -->\n      <div v-else-if=\"editingTemplate\" class=\"gc-prompt-editor\">\n        <div class=\"gc-editor-header\">\n          <button class=\"gc-btn-link\" @click=\"cancelEdit\">\n            <i class=\"fa-solid fa-arrow-left\"></i> \n          </button>\n          <span class=\"gc-editor-title\">: {{ editingTemplate.name }}</span>\n        </div>\n        <div class=\"gc-editor-form\">\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <input v-model=\"editingTemplate.name\" type=\"text\" class=\"gc-input\" :disabled=\"editingTemplate.builtin\" />\n          </div>\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <input v-model=\"editingTemplate.description\" type=\"text\" class=\"gc-input\" />\n          </div>\n          <div class=\"gc-setting-item\">\n            <label></label>\n            <div class=\"gc-editor-hint\" v-pre>\n              : <code>{{}}</code> <code>{{_}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code> <code>{{}}</code>\n            </div>\n            <textarea \n              v-model=\"editingTemplate.content\" \n              class=\"gc-textarea gc-editor-textarea\"\n              rows=\"12\"\n            ></textarea>\n          </div>\n          <div class=\"gc-editor-actions\">\n            <GcButton @click=\"cancelEdit\"></GcButton>\n            <GcButton variant=\"primary\" @click=\"saveTemplate\">\n              <i class=\"fa-solid fa-save\"></i> \n            </GcButton>\n          </div>\n        </div>\n      </div>\n    </div>\n  </GcOverlay>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { GcOverlay, GcSwitch, GcButton, GcBadge } from '../components';\nimport type { PromptTemplate, PromptTemplateType } from '../../types';\n\ndefineProps<{\n  visible: boolean;\n  templates: PromptTemplate[];\n}>();\n\nconst emit = defineEmits<{\n  (e: 'close'): void;\n  (e: 'reset'): void;\n  (e: 'toggle', id: string): void;\n  (e: 'move', id: string, direction: 'up' | 'down'): void;\n  (e: 'delete', id: string, name: string): void;\n  (e: 'create', template: Omit<PromptTemplate, 'id' | 'order' | 'builtin'>): void;\n  (e: 'save', template: PromptTemplate): void;\n}>();\n\n// \nconst isCreating = ref(false);\nconst editingTemplate = ref<PromptTemplate | null>(null);\nconst newTemplate = ref({\n  name: '',\n  description: '',\n  content: '',\n  type: 'footer' as PromptTemplateType,\n  enabled: true,\n});\n\nconst startCreate = () => {\n  newTemplate.value = {\n    name: '',\n    description: '',\n    content: '',\n    type: 'footer',\n    enabled: true,\n  };\n  isCreating.value = true;\n};\n\nconst cancelCreate = () => {\n  isCreating.value = false;\n};\n\nconst createTemplate = () => {\n  if (!newTemplate.value.name.trim()) {\n    toastr.warning('');\n    return;\n  }\n  emit('create', {\n    name: newTemplate.value.name.trim(),\n    description: newTemplate.value.description.trim(),\n    content: newTemplate.value.content,\n    type: newTemplate.value.type,\n    enabled: newTemplate.value.enabled,\n  });\n  isCreating.value = false;\n};\n\nconst startEdit = (template: PromptTemplate) => {\n  editingTemplate.value = { ...template };\n};\n\nconst cancelEdit = () => {\n  editingTemplate.value = null;\n};\n\nconst saveTemplate = () => {\n  if (editingTemplate.value) {\n    emit('save', editingTemplate.value);\n    editingTemplate.value = null;\n  }\n};\n\nconst handleClose = () => {\n  if (isCreating.value) {\n    isCreating.value = false;\n  }\n  if (editingTemplate.value) {\n    editingTemplate.value = null;\n  }\n  emit('close');\n};\n</script>\n\n<style scoped>\n.gc-prompts-content {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.gc-prompts-hint {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 10px 12px;\n  background: rgba(59, 130, 246, 0.1);\n  border-radius: 6px;\n  font-size: 0.8em;\n  color: #60a5fa;\n  margin-bottom: 8px;\n}\n\n.gc-prompts-hint i {\n  font-size: 1.1em;\n}\n\n.gc-prompts-list {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.gc-prompt-item {\n  display: flex;\n  align-items: flex-start;\n  gap: 10px;\n  padding: 10px 12px;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  transition: all 0.2s;\n}\n\n.gc-prompt-item:hover {\n  background: rgba(0, 0, 0, 0.3);\n}\n\n.gc-prompt-item.disabled {\n  opacity: 0.6;\n}\n\n.gc-prompt-toggle {\n  padding-top: 2px;\n}\n\n.gc-prompt-info {\n  flex: 1;\n  cursor: pointer;\n  min-width: 0;\n}\n\n.gc-prompt-name {\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n  gap: 6px;\n  margin-bottom: 4px;\n  font-weight: 500;\n  font-size: 0.9em;\n}\n\n.gc-prompt-type {\n  font-size: 0.75em;\n  color: var(--gc-text-muted, #94a3b8);\n  padding: 2px 6px;\n  background: rgba(255, 255, 255, 0.05);\n  border-radius: 4px;\n}\n\n.gc-prompt-desc {\n  font-size: 0.8em;\n  color: var(--gc-text-muted, #94a3b8);\n}\n\n.gc-prompt-actions {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.gc-prompt-order-btns {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n}\n\n.gc-btn-icon-xs {\n  width: 20px;\n  height: 16px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: rgba(255, 255, 255, 0.05);\n  border: none;\n  border-radius: 3px;\n  color: var(--gc-text-muted, #94a3b8);\n  cursor: pointer;\n  font-size: 0.7em;\n}\n\n.gc-btn-icon-xs:hover {\n  background: rgba(255, 255, 255, 0.1);\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-btn-icon-sm {\n  width: 28px;\n  height: 28px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: rgba(255, 255, 255, 0.05);\n  border: none;\n  border-radius: 4px;\n  color: var(--gc-text-muted, #94a3b8);\n  cursor: pointer;\n}\n\n.gc-btn-icon-sm:hover {\n  background: rgba(255, 255, 255, 0.1);\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-btn-icon-sm.danger:hover {\n  background: rgba(239, 68, 68, 0.2);\n  color: #f87171;\n}\n\n.gc-add-template-btn {\n  margin-top: 8px;\n}\n\n.gc-prompt-editor {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.gc-editor-header {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.gc-editor-title {\n  font-weight: 600;\n  font-size: 0.95em;\n}\n\n.gc-editor-form {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.gc-setting-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.gc-setting-item label {\n  font-size: 0.85em;\n  color: var(--gc-text-muted, #94a3b8);\n  font-weight: 500;\n}\n\n.gc-input {\n  padding: 8px 10px;\n  background: rgba(0, 0, 0, 0.2);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 6px;\n  color: var(--gc-text, #f1f5f9);\n  font-size: 0.9em;\n}\n\n.gc-input:focus {\n  outline: none;\n  border-color: var(--gc-primary, #f472b6);\n}\n\n.gc-textarea {\n  padding: 8px 10px;\n  background: rgba(0, 0, 0, 0.2);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 6px;\n  color: var(--gc-text, #f1f5f9);\n  font-size: 0.9em;\n  resize: vertical;\n  font-family: inherit;\n}\n\n.gc-textarea:focus {\n  outline: none;\n  border-color: var(--gc-primary, #f472b6);\n}\n\n.gc-editor-textarea {\n  min-height: 200px;\n}\n\n.gc-editor-hint {\n  font-size: 0.75em;\n  color: var(--gc-text-muted, #94a3b8);\n  padding: 6px 10px;\n  background: rgba(0, 0, 0, 0.15);\n  border-radius: 4px;\n  margin-bottom: 6px;\n}\n\n.gc-editor-hint code {\n  padding: 1px 4px;\n  background: rgba(255, 255, 255, 0.1);\n  border-radius: 3px;\n  font-family: monospace;\n  font-size: 0.95em;\n}\n\n.gc-setting-toggle {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 8px 0;\n}\n\n.gc-setting-toggle label {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n}\n\n.gc-setting-toggle label span {\n  font-size: 0.85em;\n  font-weight: 500;\n}\n\n.gc-editor-actions {\n  display: flex;\n  gap: 8px;\n  justify-content: flex-end;\n  margin-top: 8px;\n}\n\n.gc-btn-link {\n  background: transparent;\n  border: none;\n  color: var(--gc-text-muted, #94a3b8);\n  cursor: pointer;\n  padding: 4px 8px;\n  font-size: 0.9em;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.gc-btn-link:hover {\n  color: var(--gc-text, #f1f5f9);\n}\n</style>\n"],"sourceRoot":""}]);
