-radius: 8px;\n  flex-wrap: wrap;\n}\n\n.gc-debug-tab {\n  flex: 1;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 6px;\n  padding: 8px 12px;\n  background: transparent;\n  border: none;\n  border-radius: 6px;\n  color: var(--gc-text-muted, #94a3b8);\n  font-size: 0.8em;\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.gc-debug-tab:hover {\n  background: rgba(255, 255, 255, 0.05);\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-debug-tab.active {\n  background: var(--gc-primary, #f472b6);\n  color: white;\n}\n\n.gc-debug-tab-badge {\n  padding: 1px 6px;\n  background: rgba(255, 255, 255, 0.2);\n  border-radius: 10px;\n  font-size: 0.85em;\n}\n\n.gc-debug-tab.active .gc-debug-tab-badge {\n  background: rgba(255, 255, 255, 0.3);\n}\n\n.gc-debug-tab-content {\n  padding: 8px 0;\n}\n\n.gc-btn-link {\n  background: transparent;\n  border: none;\n  color: var(--gc-text-muted, #94a3b8);\n  cursor: pointer;\n  padding: 4px 8px;\n}\n\n.gc-btn-link:hover {\n  color: var(--gc-text, #f1f5f9);\n}\n</style>\n"],"sourceRoot":""}]);
