etween;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.gc-debug-logs-header h3 {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 0.9em;\n  font-weight: 600;\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-debug-logs-header h3 i {\n  color: var(--gc-primary, #f472b6);\n}\n\n.gc-btn-link {\n  background: transparent;\n  border: none;\n  color: var(--gc-text-muted, #94a3b8);\n  cursor: pointer;\n  padding: 4px 8px;\n}\n\n.gc-btn-link:hover {\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-debug-logs {\n  flex: 1;\n  background: rgba(0, 0, 0, 0.3);\n  border-radius: 8px;\n  padding: 10px;\n  font-family: 'Monaco', 'Menlo', monospace;\n  font-size: 0.8em;\n  max-height: 400px;\n  overflow-y: auto;\n}\n\n.gc-debug-log-item {\n  display: flex;\n  gap: 10px;\n  padding: 4px 0;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.05);\n}\n\n.gc-debug-log-item:last-child {\n  border-bottom: none;\n}\n\n.gc-debug-log-item.warn {\n  color: #fbbf24;\n}\n\n.gc-debug-log-item.error {\n  color: #f87171;\n}\n\n.gc-log-time {\n  color: var(--gc-text-muted, #94a3b8);\n  white-space: nowrap;\n}\n\n.gc-log-msg {\n  word-break: break-all;\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-debug-empty {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 40px 20px;\n  text-align: center;\n}\n\n.gc-debug-empty i {\n  font-size: 2em;\n  color: var(--gc-text-muted, #94a3b8);\n  margin-bottom: 12px;\n}\n\n.gc-debug-empty p {\n  font-size: 0.9em;\n  margin-bottom: 6px;\n}\n\n.gc-debug-empty small {\n  font-size: 0.8em;\n  color: var(--gc-text-muted, #94a3b8);\n}\n</style>\n"],"sourceRoot":""}]);
