,YAAa,CACb,6BAA8B,CAC9B,kBAAmB,CACnB,eAAgB,CAChB,eAAgB,CAChB,2BAA+B,CAC/B,iBACF,CAEA,sCACE,4BAA8B,CAC9B,MAAO,CACP,eAAgB,CAChB,sBAAuB,CACvB,kBACF,CAEA,4CACE,aAAc,CACd,+BAAgC,CAChC,eAAgB,CAChB,aACF,CAGA,wCACE,cAAe,CACf,eAAgB,CAChB,kCAAoC,CACpC,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,OACF,CAEA,0CACE,aACF,CAGA,kDACE,SACF,CAEA,wDACE,sBACF,CAEA,wDACE,gCAAoC,CACpC,iBACF,CAEA,8DACE,gCACF","sourcesContent":["<template>\n  <div class=\"gc-char-list\">\n    <div v-if=\"characters.size === 0\" class=\"gc-empty\">\n      <i class=\"fa-regular fa-folder-open\"></i>\n      <span></span>\n    </div>\n    \n    <CharacterCard\n      v-for=\"[name, data] in characters\"\n      :key=\"name\"\n      :name=\"name\"\n      :data=\"data\"\n      :show-damage=\"showDamage\"\n      :expanded=\"expandedChar === name\"\n      @toggle=\"toggleExpand\"\n    >\n      <!--  -->\n      <template #damage=\"{ damageData }\">\n        <div v-if=\"showDamage && damageData\" class=\"gc-detail-section full-width gc-damage-section\">\n          <div class=\"gc-detail-section-title\">\n            <i class=\"fa-solid fa-explosion\"></i> \n            <span class=\"gc-damage-level-badge\">{{ damageData. }}</span>\n          </div>\n          <div class=\"gc-damage-grid\">\n            <div class=\"gc-damage-item\">\n              <div class=\"gc-damage-item-label\"></div>\n              <div class=\"gc-damage-item-value\">{{ damageData.._ }}</div>\n            </div>\n            <div class=\"gc-damage-item\">\n              <div class=\"gc-damage-item-label\"></div>\n              <div class=\"gc-damage-item-value casualties\">{{ damageData... }}</div>\n            </div>\n            <div class=\"gc-damage-item\">\n              <div class=\"gc-damage-item-label\"></div>\n              <div class=\"gc-damage-item-value buildings\">{{ damageData... }}</div>\n            </div>\n            <div v-if=\"damageData... > 0\" class=\"gc-damage-item\">\n              <div class=\"gc-damage-item-label\"></div>\n              <div class=\"gc-damage-item-value\">{{ damageData... }}</div>\n            </div>\n          </div>\n          \n          <!--  -->\n          <div v-if=\"damageData.\" class=\"gc-macro-damage\">\n            <div class=\"gc-macro-damage-title\">\n              <i class=\"fa-solid fa-globe\"></i>\n              : {{ damageData.. }}\n            </div>\n            <div class=\"gc-macro-damage-list\">\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n              <span v-if=\"damageData..\">{{ damageData... }}</span>\n            </div>\n          </div>\n          \n          <!--  -->\n          <div v-if=\"damageData..length > 0\" class=\"gc-special-effects\">\n            <div class=\"gc-special-effects-title\">\n              <i class=\"fa-solid fa-bolt\"></i> \n            </div>\n            <div class=\"gc-special-effects-list\">\n              <span v-for=\"(effect, i) in damageData..slice(0, 4)\" :key=\"i\" class=\"gc-effect-tag\">\n                {{ effect }}\n              </span>\n              <span v-if=\"damageData..length > 4\" class=\"gc-effect-more\">\n                +{{ damageData..length - 4 }}\n              </span>\n            </div>\n          </div>\n        </div>\n      </template>\n\n      <!--  -->\n      <template #actual-damage=\"{ actualDamage }\">\n        <div v-if=\"actualDamage\" class=\"gc-detail-section full-width gc-actual-damage-section\">\n          <div class=\"gc-detail-section-title\">\n            <i class=\"fa-solid fa-skull\"></i> \n            <span class=\"gc-actual-damage-badge\"></span>\n          </div>\n          <div class=\"gc-actual-damage-grid\">\n            <div v-if=\"actualDamage.\" class=\"gc-actual-damage-item\">\n              <div class=\"gc-actual-damage-item-label\"></div>\n              <div class=\"gc-actual-damage-item-value casualties\">{{ formatLargeNumber(actualDamage.) }}</div>\n            </div>\n            <div v-if=\"actualDamage.\" class=\"gc-actual-damage-item\">\n              <div class=\"gc-actual-damage-item-label\"></div>\n              <div class=\"gc-actual-damage-item-value buildings\">{{ actualDamage. }}</div>\n            </div>\n            <div v-if=\"actualDamage.\" class=\"gc-actual-damage-item\">\n              <div class=\"gc-actual-damage-item-label\"></div>\n              <div class=\"gc-actual-damage-item-value cities\">{{ actualDamage. }}</div>\n            </div>\n          </div>\n          \n          <!--  -->\n          <div v-if=\"actualDamage.\" class=\"gc-last-action\">\n            <div class=\"gc-last-action-title\">\n              <i class=\"fa-solid fa-shoe-prints\"></i> \n            </div>\n            <div class=\"gc-last-action-content\">\n              <span class=\"gc-last-action-desc\">{{ actualDamage.. }}</span>\n              <span v-if=\"actualDamage..\" class=\"gc-last-action-casualties\">\n                : {{ formatLargeNumber(actualDamage..) }}\n              </span>\n            </div>\n          </div>\n          \n          <!--  -->\n          <div v-if=\"actualDamage.?.length\" class=\"gc-major-events\">\n            <div class=\"gc-major-events-title\">\n              <i class=\"fa-solid fa-star\"></i>  ({{ actualDamage..length }})\n            </div>\n            <div class=\"gc-major-events-list\">\n              <div v-for=\"(event, i) in actualDamage..slice(-3)\" :key=\"i\" class=\"gc-major-event-item\">\n                <span class=\"gc-major-event-desc\">{{ event. }}</span>\n                <span v-if=\"event.\" class=\"gc-major-event-casualties\">{{ formatLargeNumber(event.) }}</span>\n              </div>\n            </div>\n          </div>\n          \n          <!--  -->\n          <div v-if=\"actualDamage.\" class=\"gc-actual-damage-note\">\n            <i class=\"fa-solid fa-comment\"></i> {{ actualDamage. }}\n          </div>\n        </div>\n      </template>\n    </CharacterCard>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport CharacterCard from './features/CharacterCard.vue';\nimport type { CharacterData } from '../characters';\n\ndefineProps<{\n  characters: Map<string, CharacterData>;\n  showDamage?: boolean;\n}>();\n\nconst expandedChar = ref<string | null>(null);\n\nconst toggleExpand = (name: string) => {\n  expandedChar.value = expandedChar.value === name ? null : name;\n};\n\n// \nconst formatLargeNumber = (n: number): string => {\n  if (n >= 1e8) return `${(n / 1e8).toFixed(1)}`;\n  if (n >= 1e4) return `${(n / 1e4).toFixed(1)}`;\n  if (n >= 1000) return `${(n / 1000).toFixed(1)}`;\n  return Math.round(n).toString();\n};\n</script>\n\n<style scoped>\n.gc-char-list {\n  display: flex;\n  flex-direction: column;\n  gap: 0;\n  max-height: 600px;\n  overflow-y: auto;\n  padding-right: 4px;\n}\n\n.gc-empty {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 16px;\n  color: var(--gc-text-muted, #94a3b8);\n  padding: 40px 24px;\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 16px;\n  border: 1px dashed var(--gc-border, rgba(255, 255, 255, 0.08));\n}\n\n.gc-damage-section {\n  background: rgba(249, 115, 22, 0.05);\n  border-radius: 8px;\n  padding: 12px !important;\n  border: 1px solid rgba(249, 115, 22, 0.15);\n}\n\n.gc-damage-section .gc-detail-section-title {\n  color: #fb923c;\n}\n\n.gc-damage-level-badge {\n  font-size: 0.8em;\n  font-weight: 500;\n  padding: 2px 8px;\n  background: rgba(249, 115, 22, 0.2);\n  color: #fb923c;\n  border-radius: 4px;\n  margin-left: auto;\n}\n\n.gc-damage-grid {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 8px;\n  margin-top: 8px;\n}\n\n.gc-damage-item {\n  background: rgba(0, 0, 0, 0.2);\n  padding: 8px 10px;\n  border-radius: 6px;\n}\n\n.gc-damage-item-label {\n  font-size: 0.75em;\n  color: var(--gc-text-muted, #94a3b8);\n  margin-bottom: 2px;\n}\n\n.gc-damage-item-value {\n  font-size: 0.9em;\n  font-weight: 500;\n  font-family: var(--gc-font-mono);\n  color: var(--gc-text, #f8fafc);\n}\n\n.gc-damage-item-value.casualties {\n  color: #f87171;\n}\n\n.gc-damage-item-value.buildings {\n  color: #fbbf24;\n}\n\n/*  */\n.gc-macro-damage {\n  margin-top: 10px;\n  padding-top: 10px;\n  border-top: 1px dashed rgba(249, 115, 22, 0.2);\n}\n\n.gc-macro-damage-title {\n  font-size: 0.8em;\n  font-weight: 600;\n  color: #fb923c;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  margin-bottom: 6px;\n}\n\n.gc-macro-damage-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 6px;\n}\n\n.gc-macro-damage-list span {\n  font-size: 0.8em;\n  padding: 3px 8px;\n  background: rgba(249, 115, 22, 0.15);\n  color: var(--gc-text, #f8fafc);\n  border-radius: 4px;\n}\n\n/*  */\n.gc-special-effects {\n  margin-top: 10px;\n  padding-top: 10px;\n  border-top: 1px dashed rgba(249, 115, 22, 0.2);\n}\n\n.gc-special-effects-title {\n  font-size: 0.8em;\n  font-weight: 600;\n  color: #60a5fa;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  margin-bottom: 6px;\n}\n\n.gc-special-effects-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 4px;\n}\n\n.gc-effect-tag {\n  font-size: 0.75em;\n  padding: 2px 6px;\n  background: rgba(96, 165, 250, 0.15);\n  color: #60a5fa;\n  border-radius: 4px;\n}\n\n.gc-effect-more {\n  font-size: 0.75em;\n  padding: 2px 6px;\n  background: rgba(255, 255, 255, 0.1);\n  color: var(--gc-text-muted, #94a3b8);\n  border-radius: 4px;\n}\n\n/*  */\n.gc-actual-damage-section {\n  background: rgba(239, 68, 68, 0.05);\n  border-radius: 8px;\n  padding: 12px !important;\n  border: 1px solid rgba(239, 68, 68, 0.2);\n  margin-top: 8px;\n}\n\n.gc-actual-damage-section .gc-detail-section-title {\n  color: #ef4444;\n}\n\n.gc-actual-damage-badge {\n  font-s