/**
 *  - 
 */

let precision = 2;

export function setPrecision(p: number): void {
  precision = p;
}

export function getPrecision(): number {
  return precision;
}

/**
 * 
 */
export function round(value: number, p: number = precision): number {
  const factor = Math.pow(10, p);
  return Math.round(value * factor) / factor;
}

/**
 *  - 
 */
export function formatLength(meters: number, useAstronomical = true): string {
  const abs = Math.abs(meters);

  // 
  if (useAstronomical) {
    const LIGHT_YEAR = 9460730472580800;
    const AU = 149597870700;

    if (abs >= LIGHT_YEAR * 1000) {
      return `${round(meters / LIGHT_YEAR)}`;
    }
    if (abs >= LIGHT_YEAR) {
      return `${round(meters / LIGHT_YEAR, 3)}`;
    }
    if (abs >= AU) {
      return `${round(meters / AU)}AU`;
    }
  }

  // 
  if (abs >= 1000000) {
    return `${round(meters / 1000)}`;
  }
  if (abs >= 1000) {
    return `${round(meters / 1000)}`;
  }
  if (abs >= 1) {
    return `${round(meters)}`;
  }
  if (abs >= 0.01) {
    return `${round(meters * 100)}`;
  }
  if (abs >= 0.001) {
    return `${round(meters * 1000)}`;
  }
  if (abs >= 0.000001) {
    return `${round(meters * 1000000)}`;
  }
  if (abs >= 0.000000001) {
    return `${round(meters * 1000000000)}`;
  }
  return `${round(meters * 1000000000000)}`;
}

/**
 * 
 */
export function formatWeight(kg: number): string {
  if (kg >= 1e12) {
    return `${round(kg / 1e12)}`;
  }
  if (kg >= 1e9) {
    return `${round(kg / 1e9)}`;
  }
  if (kg >= 1e6) {
    return `${round(kg / 1e6)}`;
  }
  if (kg >= 1000) {
    return `${round(kg / 1000)}`;
  }
  return `${round(kg)}`;
}

/**
 * 
 */
export function formatVolume(cubicMeters: number): string {
  if (cubicMeters >= 1e9) {
    return `${round(cubicMeters / 1e9)}`;
  }
  if (cubicMeters >= 1) {
    return `${round(cubicMeters)}`;
  }
  if (cubicMeters >= 0.001) {
    return `${round(cubicMeters * 1000)}`;
  }
  if (cubicMeters >= 0.000001) {
    return `${round(cubicMeters * 1000000)}`;
  }
  return `${round(cubicMeters * 1e9)}`;
}

/**
 * 
 */
export function formatArea(sqMeters: number): string {
  if (sqMeters >= 1e6) {
    return `${round(sqMeters / 1e6)}`;
  }
  if (sqMeters >= 1) {
    return `${round(sqMeters)}`;
  }
  if (sqMeters >= 0.0001) {
    return `${round(sqMeters * 10000)}`;
  }
  return `${round(sqMeters * 1000000)}`;
}
