      :value=\"testHeight\" \n            @input=\"$emit('update:test-height', parseFloat(($event.target as HTMLInputElement).value) || 0)\" \n            type=\"number\" \n            placeholder=\"(m)\" \n            class=\"gc-input sm\" \n          />\n          <input \n            :value=\"testOriginal\" \n            @input=\"$emit('update:test-original', parseFloat(($event.target as HTMLInputElement).value) || 0)\" \n            type=\"number\" \n            placeholder=\"(m)\" \n            class=\"gc-input sm\" \n          />\n        </div>\n        <div class=\"gc-debug-hint\">\n          <i class=\"fa-solid fa-info-circle\"></i>\n          <span v-if=\"testHeight && testOriginal && testHeight < testOriginal\">\n            <strong class=\"tiny\"></strong>{{ (testHeight / testOriginal).toFixed(4) }}\n          </span>\n          <span v-else-if=\"testHeight && testOriginal\">\n            <strong class=\"giant\"></strong>{{ (testHeight / testOriginal).toFixed(1) }}\n          </span>\n          <span v-else></span>\n        </div>\n        <div class=\"gc-debug-btn-row\">\n          <button class=\"gc-btn gc-btn-sm primary\" @click=\"$emit('inject')\">\n            <i class=\"fa-solid fa-syringe\"></i> \n          </button>\n          <button class=\"gc-btn gc-btn-sm\" @click=\"$emit('clear', testName)\" :disabled=\"!testName\">\n            <i class=\"fa-solid fa-eraser\"></i> \n          </button>\n          <button class=\"gc-btn gc-btn-sm danger\" @click=\"$emit('clear')\">\n            <i class=\"fa-solid fa-trash\"></i> \n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div class=\"gc-debug-section\">\n      <h3><i class=\"fa-solid fa-lightbulb\"></i> </h3>\n      <div class=\"gc-debug-quick-tests\">\n        <button class=\"gc-btn gc-btn-sm\" @click=\"$emit('quick-test', { name: '', height: 100, original: 1.65 })\">\n           100m\n        </button>\n        <button class=\"gc-btn gc-btn-sm\" @click=\"$emit('quick-test', { name: '', height: 1000, original: 1.65 })\">\n           1km\n        </button>\n        <button class=\"gc-btn gc-btn-sm\" @click=\"$emit('quick-test', { name: '', height: 10000000, original: 1.65 })\">\n           10Mm\n        </button>\n        <button class=\"gc-btn gc-btn-sm tiny\" @click=\"$emit('quick-test', { name: '', height: 0.01, original: 1.65 })\">\n           1cm\n        </button>\n        <button class=\"gc-btn gc-btn-sm tiny\" @click=\"$emit('quick-test', { name: '', height: 0.001, original: 1.65 })\">\n           1mm\n        </button>\n      </div>\n    </div>\n\n    <!--  -->\n    <div class=\"gc-debug-section\">\n      <h3><i class=\"fa-solid fa-skull\"></i> </h3>\n      <div class=\"gc-debug-form\">\n        <div class=\"gc-debug-hint damage\">\n          <i class=\"fa-solid fa-info-circle\"></i>\n          <span></span>\n        </div>\n        \n        <div class=\"gc-setting-item\">\n          <label></label>\n          <select v-model=\"damageTarget\" class=\"gc-input\">\n            <option value=\"\">--  --</option>\n            <option \n              v-for=\"char in giantCharacters\" \n              :key=\"char.name\" \n              :value=\"char.name\"\n            >\n              {{ char.name }}\n            </option>\n          </select>\n        </div>\n        \n        <div v-if=\"damageTarget\" class=\"gc-actual-damage-form\">\n          <div class=\"gc-debug-input-group\">\n            <label></label>\n            <div class=\"gc-debug-input-row\">\n              <input \n                v-model.number=\"damageForm.totalCasualties\" \n                type=\"number\" \n                class=\"gc-input sm\" \n                placeholder=\"0\" \n              />\n              <span class=\"gc-input-suffix\"></span>\n            </div>\n          </div>\n          \n          <div class=\"gc-debug-input-group\">\n            <label></label>\n            <div class=\"gc-debug-input-row\">\n              <input \n                v-model.number=\"damageForm.totalBuildings\" \n                type=\"number\" \n                class=\"gc-input sm\" \n                placeholder=\"0\" \n              />\n              <span class=\"gc-input-suffix\"></span>\n            </div>\n          </div>\n          \n          <div class=\"gc-debug-input-group\">\n            <label></label>\n            <input \n              v-model=\"damageForm.lastAction\" \n              type=\"text\" \n              class=\"gc-input\" \n              placeholder=\"\" \n            />\n          </div>\n          \n          <div class=\"gc-debug-input-group\">\n            <label></label>\n            <div class=\"gc-debug-input-row\">\n              <input \n                v-model.number=\"damageForm.lastActionCasualties\" \n                type=\"number\" \n                class=\"gc-input sm\" \n                placeholder=\"0\" \n              />\n              <span class=\"gc-input-suffix\"></span>\n            </div>\n          </div>\n          \n          <div class=\"gc-debug-input-group\">\n            <label></label>\n            <input \n              v-model=\"damageForm.note\" \n              type=\"text\" \n              class=\"gc-input\" \n              placeholder=\"\" \n            />\n          </div>\n          \n          <div class=\"gc-debug-btn-row\">\n            <button class=\"gc-btn gc-btn-sm primary\" @click=\"saveDamage\">\n              <i class=\"fa-solid fa-save\"></i> \n            </button>\n            <button class=\"gc-btn gc-btn-sm danger\" @click=\"clearDamage\">\n              <i class=\"fa-solid fa-trash\"></i> \n            </button>\n          </div>\n        </div>\n        \n        <div v-else class=\"gc-debug-hint muted\">\n          <i class=\"fa-solid fa-exclamation-circle\"></i>\n          <span></span>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, computed } from 'vue';\nimport type { CharacterData } from '../../../types';\n\nconst props = defineProps<{\n  testName: string;\n  testHeight: number;\n  testOriginal: number;\n  characters: CharacterData[];\n}>();\n\nconst emit = defineEmits<{\n  (e: 'update:test-name', value: string): void;\n  (e: 'update:test-height', value: number): void;\n  (e: 'update:test-original', value: number): void;\n  (e: 'inject'): void;\n  (e: 'clear', name?: string): void;\n  (e: 'quick-test', params: { name: string; height: number; original: number }): void;\n  (e: 'save-damage', params: { name: string; data: typeof damageForm.value }): void;\n  (e: 'clear-damage', name: string): void;\n}>();\n\nconst damageTarget = ref('');\nconst damageForm = ref({\n  totalCasualties: 0,\n  totalBuildings: 0,\n  lastAction: '',\n  lastActionCasualties: 0,\n  note: '',\n});\n\nconst giantCharacters = computed(() => \n  props.characters.filter(c => {\n    const scale = c.currentHeight / c.originalHeight;\n    return scale >= 1;\n  })\n);\n\nconst saveDamage = () => {\n  if (!damageTarget.value) return;\n  emit('save-damage', {\n    name: damageTarget.value,\n    data: { ...damageForm.value },\n  });\n};\n\nconst clearDamage = () => {\n  if (!damageTarget.value) return;\n  emit('clear-damage', damageTarget.value);\n  damageForm.value = {\n    totalCasualties: 0,\n    totalBuildings: 0,\n    lastAction: '',\n    lastActionCasualties: 0,\n    note: '',\n  };\n};\n</script>\n\n<style scoped>\n.gc-debug-section {\n  padding: 12px;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  margin-bottom: 12px;\n}\n\n.gc-debug-section:last-child {\n  margin-bottom: 0;\n}\n\n.gc-debug-section h3 {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 0.9em;\n  font-weight: 600;\n  margin-bottom: 10px;\n  color: var(--gc-text, #f1f5f9);\n}\n\n.gc-debug-section h3 i {\n  color: var(--gc-primary, #f472b6);\n}\n\n.gc-debug-form {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.gc-debug-input-row {\n  display: flex;\n  gap: 6px;\n  align-items: center;\n}\n\n.gc-debug-input-row .gc-input {\n  flex: 1;\n}\n\n.gc-input {\n  width: 100%;\n  padding: 6px 10px;\n  background: rgba(0, 0, 0, 0.2);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 6px;\n  color: var(--gc-text, #f1f5f9);\n  font-size: 0.85em;\n}\n\n.gc-input.sm {\n  padding: 4px 8px;\n  font-size: 0.8em;\n}\n\n.gc-debug-hint {\n  display: flex;\n  align-items: flex-start;\n  gap: 6px;\n  padding: 8px 10px;\n  background: rgba(59, 130, 246, 0.1);\n  border-radius: 6px;\n  font-size: 0.8em;\n  color: #60a5fa;\n}\n\n.gc-debug-hint i {\n  margin-top: 2px;\n}\n\n.gc-debug-hint strong.tiny {\n  color: #60a5fa;\n}\n\n.gc-debug-hint strong.giant {\n  color: var(--gc-primary, #f472b6);\n}\n\n.gc-debug-hint.damage {\n  background: rgba(239, 68, 68, 0.1);\n  color: #f87171;\n}\n\n.gc-debug-hint.muted {\n  background: rgba(148, 163, 184, 0.1);\n  color: var(--gc-text-muted, #94a3b8);\n}\n\n.gc-debug-btn-row {\n  display: flex;\n  gap: 6px;\n  flex-wrap: wrap;\n}\n\n.gc-btn {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  gap: 6px;\n  padding: 8px 14px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid var(--gc-border, rgba(255, 255, 255, 0.1));\n  border-radius: 8px;\n  color: var(--gc-text, #f1f5f9);\n  font-weight: 500;\n  font-size: 0.85em;\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.gc-btn:hover {\n  background: rgba(255, 255, 255, 0.1);\n}\n\n.gc-btn-sm {\n  padding: 6px 10px;\n  font-size: 0.8em;\n}\n\n.gc-btn.primary {\n  background: var(--gc-primary, #f472b6);\n  border-color: var(--gc-primary, #f472b6);\n  color: white;\n}\n\n.gc-btn.primary:hover {\n  background: var(--gc-primary-hover, #ec4899);\n}\n\n.gc-btn.danger {\n  background: rgba(239, 68, 68, 0.1);\n  border-color: rgba(239, 68, 68, 0.3);\n  color: #f87171;\n}\n\n.gc-btn.danger:hover {\n  background: rgba(239, 68, 68, 0.2);\n}\n\n.gc-btn:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.gc-debug-quick-tests {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 6px;\n}\n\n.gc-debug-quick-tests .gc-btn.tiny {\n  background: rgba(96, 165, 250, 0.1);\n  border-color: rgba(96, 165, 250, 0.3);\n  color: #60a5fa;\n}\n\n.gc-debug-quick-tests .gc-btn.tiny:hover {\n  background: rgba(96, 165, 250, 0.2);\n}\n\n.gc-setting-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.gc-setting-item label {\n  font-size: 0.85em;\n  color: var(--gc-text-muted, #94a3b8);\n}\n\n.gc-actual-damage-form {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  padding: 10px;\n  background: rgba(239, 68, 68, 0.05);\n  border-radius: 6px;\n  border: 1px solid rgba(239, 68, 68, 0.1);\n}\n\n.gc-debug-input-group {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.gc-debug-input-group label {\n  font-size: 0.8em;\n  color: var(--gc-text-muted, #94a3b8);\n}\n\n.gc-input-suffix {\n  color: var(--gc-text-muted, #94a3b8);\n  font-size: 0.85em;\n  white-space: nowrap;\n}\n</style>\n"],"sourceRoot":""}]);
