k: 0;\n  background: rgba(255, 255, 255, 0.05);\n  color: #94a3b8;\n  transition: all 0.3s;\n}\n\n.gc-char-avatar.giant {\n  background: linear-gradient(135deg, rgba(249, 115, 22, 0.2), rgba(239, 68, 68, 0.2));\n  color: #f97316;\n  box-shadow: 0 0 15px rgba(249, 115, 22, 0.1);\n}\n\n.gc-char-avatar.tiny {\n  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2), rgba(99, 102, 241, 0.2));\n  color: #60a5fa;\n  box-shadow: 0 0 15px rgba(59, 130, 246, 0.1);\n}\n\n.gc-char-info {\n  flex: 1;\n  min-width: 0;\n}\n\n.gc-char-name {\n  font-weight: 600;\n  font-size: 1.05em;\n  color: #f1f5f9;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 4px;\n}\n\n.gc-char-expand-icon {\n  font-size: 0.8em;\n  color: #94a3b8;\n  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n  opacity: 0.5;\n}\n\n.gc-char-item:hover .gc-char-expand-icon {\n  opacity: 1;\n}\n\n.gc-char-item.expanded .gc-char-expand-icon {\n  transform: rotate(90deg);\n  color: var(--gc-primary, #ec4899);\n}\n\n.gc-char-stats {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  font-size: 0.9em;\n}\n\n.gc-stat-height {\n  color: #e2e8f0;\n  font-weight: 500;\n}\n\n.gc-stat-unknown {\n  color: #64748b;\n  font-style: italic;\n}\n\n.gc-stat-multiplier {\n  color: #94a3b8;\n  font-family: var(--gc-font-mono);\n  background: rgba(255, 255, 255, 0.05);\n  padding: 1px 6px;\n  border-radius: 4px;\n  font-size: 0.85em;\n}\n\n.gc-level-badge {\n  padding: 4px 10px;\n  border-radius: 8px;\n  font-size: 0.75em;\n  font-weight: 700;\n  text-transform: uppercase;\n  background: rgba(255, 255, 255, 0.05);\n  color: #94a3b8;\n  letter-spacing: 0.05em;\n}\n\n.gc-level-badge.giant {\n  background: rgba(249, 115, 22, 0.15);\n  color: #f97316;\n}\n\n.gc-level-badge.tiny {\n  background: rgba(59, 130, 246, 0.15);\n  color: #60a5fa;\n}\n\n/*  */\n.gc-damage-indicator {\n  display: inline-flex;\n  align-items: center;\n  gap: 4px;\n  font-size: 0.8em;\n  padding: 2px 8p